.task {
    background-color: $_white;
    padding: 20px 0;
}

.task-wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 -15px;

    @media (max-width: 1058px) {
        justify-content: center;
    }
}

.task-todo {
    border-radius: 10px;
    overflow: hidden;
    @include shadow;
    display: flex;
    flex-direction: column;
    margin: 0 15px 15px 0;

    @media (max-width: 1170px) {
        margin: 0 15px 15px 15px;
    }

    @media (max-width: 360px) {
        margin: 0 0 15px 0;
    }

    &:last-child {
        margin-right: 0;

        @media (max-width: 1170px) {
            margin-right: 15px;
        }

        @media (max-width: 540px) {
            margin-right: 0;
        }
    }
}

.task-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border-bottom: 1px solid $_gray;
}

.task-header-title, .task-header-count {
    @include h2;
}

.task-container {
    padding: 10px;
    flex-grow: 1;
    overflow-y: auto;
    max-height: 62vh;

    @media (max-width: 320px) {
        overflow-y: hidden;
        max-height: none;
    }
}

.task-card {
    width: 300px;
    border-radius: 20px;
    padding: 10px;
    margin-bottom: 10px;
    @include shadow;
    cursor: move;
    user-select: none;

    @media (max-width: 1058px) {
        width: 480px;
    }

    @media (max-width: 540px) {
        width: 300px;
    }

    @media (max-width: 360px) {
        width: 290px;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.task-card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.task-card-header-title {
    @include h3;
}

.task-card-header-actions button {
    padding: 6px;
    border-radius: 6px;
    text-transform: uppercase;
    margin-right: 4px;
    @include button;

    &:last-child {
        margin-right: 0;
    }
}

.task-card-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    min-height: 60px;

    button {
        align-self: stretch;
        border-radius: 6px;
        padding: 6px;
        @include button;
    }
}

.task-card-main-description {
    margin-right: 6px;
    @include text;
}

.task-card-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.task-card-footer-user, .task-card-footer-time {
    @include h4;
}

.task-footer {
    border-top: 1px solid $_gray;

    button {
        width: 100%;
        padding: 10px 0;
        @include h4;
        cursor: pointer;
        transition: .2s;
    
        &:hover {
            transform: scale(1.02);
        }
    }
}

.task-color-todo {
    background-color: $_green;

    .task-footer-btn:hover {
        background-color: $_green-hover;
    }
    
    .task-footer-btn:active {
        background-color: $_green-active;
    }
}

.task-color-inprogress {
    background-color: $_gray-progress;
}

.task-color-done {
    background-color: $_blue;
    cursor: default;

    .task-footer-btn:hover {
        background-color: $_blue-hover;
    }

    .task-footer-btn:active {
        background-color: $_blue-active;
    }
}

.task-card-zero {
    text-align: center;
    cursor: default;
}

.card-drop {
    pointer-events: none;
    animation-name: opacityAnimation;
    animation-duration: .8s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;

    @keyframes opacityAnimation {
        0% {
            opacity: 1;
        }

        100% {
            opacity: .3;
        }
    }
}